.todo {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  font-size: 1.5rem;
  border-radius: 5px;
  border: 2px solid #555;
  color: #112d49;
  background-color: #fbfef9;
  user-select: none;
  max-width: 600px;
  margin: 15px auto;
  cursor: pointer;
  transition: all 0.3s ease 0s;
}
